<template>
  <div id="app">
    <div class="left">
      <h5>原文：</h5>
      <textarea rows="20" class="form-control" id="content" name="content" v-model="content"></textarea>
    </div>
    <div class="select">
      <select v-model="lang">
        <option v-for="(item,index) in langs" :key="index" :value="index">
          {{item}}
        </option>
      </select>
      <button class="btn btn-primary btn-sm" @click="translate">翻译</button>
    </div>
    <div class="right">
      <HelloWorld :msg="result"/>
    </div>
  </div>
</template>
<script>
// @ is an alias to /src
import HelloWorld from '@/components/HelloWorld.vue'

export default {
  name: 'app',
  components: {
    HelloWorld
  },
  data () {
    return {
      content:"",
      lang:'en',
      langs:[],
      result:''
    }
  },
  created() {
    this.$axios.get("https://translate.yandex.net/api/v1.5/tr.json/getLangs",{
      params:{
        key:"trnsl.1.1.20191220T031726Z.e6425bfe1e855dae.ce3bcfa9b3cf460ee155934b5b953af01be64d6f",
        ui:"zh"
      }
    }).then(res=>{
      this.langs = res.data.langs
    }).catch(err=>{
      console.log(err)
    })
  },
  methods:{
    translate(){
      this.$axios.get("https://translate.yandex.net/api/v1.5/tr.json/translate",{
        params:{
          key:"trnsl.1.1.20191220T031726Z.e6425bfe1e855dae.ce3bcfa9b3cf460ee155934b5b953af01be64d6f",
          text:this.content,
          lang:this.lang
        }
      }).then(res=>{
        this.result = res.data.text[0]
      }).catch(err=>{
        console.log(err)
      })
    }

  }
}
</script>
<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  padding: 20px;
}

.left{
  width: 40%;
  float: left;
}
.left textarea{
  width: 100%;
  padding: 10px;
  background-color: aliceblue;
}
.select{
  width: 20%;
  margin-top: 50px;
  float: left;
}
.right{
  width: 40%;
  float: right;
}
.select button{
  margin: 0 10px;
  width: 80px;
}
</style>
